
import Drawer from 'vux/src/components/drawer/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


import Menu from "components/Index/Menu";
import { mapGetters, mapMutations } from "vuex";

export default {
name: "index",
components: {
Drawer,
XHeader,
Menu,
Tab,
TabItem
},
data() {
return {
searchIptVis: false,
drawerVisibility: false,

showMode: "push",
showModeValue: "push",
showPlacement: "left",
showPlacementValue: "left"
};
},
activated() {

if (this.drawerVisibility) {
this.drawerVisibility = false;
}
},
methods: {
showDrawer() {

if (Object.keys(this.userInfo).length > 0) {
this.drawerVisibility = true;

} else {

this.$router.push("/login");
}
},
onItemClick(index) {
console.log("on item click:", index);
},

...mapMutations({
setDrawerVis: "SET_DRAWER_VIS"
})
},
computed: {
headerIcon() {
const hasLogin = Object.keys(this.userInfo).length > 0;
return hasLogin ? "fa fa-reorder" : "fa fa-user-o";
},

...mapGetters(["userInfo"])
},
watch: {
drawerVisibility(val) {
this.setDrawerVis(val);
}
}
};



